    padding-bottom: 16px; }
  .select-region__item .select2-container--default
.select2-selection--single
.select2-selection__arrow {
    top: 16px;
    right: 16px; }
  .select-region__label {
    padding-left: 16px;
    padding-top: 10px;
    padding-bottom: 10px; }
    .select-region__label img {
      width: 24px;
      height: 24px;
      margin-right: 16px; }
    .select-region__label span {
      font-size: 14px;
      line-height: 17px; } }

@media (max-width: 767px) {
  .series__tabs-container {
    padding-left: 0;
    padding-right: 0; } }

.world-wide__content {
  display: flex;
  justify-content: space-between; }

.world-wide__main {
  width: calc((100% - 40px) / 3);
  padding: 32px;
  background-color: #f32424; }

.world-wide__items {
  width: calc((100% - 40px) / 3 * 2 + 20px);
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-between; }

.world-wide__slogan {
  display: block;
  font-weight: 600;
  font-size: 32px;
  line-height: 38px;
  color: #fff;
  margin: 0 0 24px;
  min-height: 391px;
  max-width: 345px; }

.world-wide__item {
  height: 100%;
  background-color: #f3f3f3;
  width: calc((100% - 20px) / 2);
  padding: 32px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end; }
  .world-wide__item:last-child {
    margin-left: auto; }
  .world-wide__item.half {
    height: calc((100% - 20px) / 2); }
  .world-wide__item.rus {
    background: linear-gradient(180deg, rgba(0, 100, 143, 0) 0%, rgba(0, 27, 68, 0.8) 100%), no-repeat center/cover url("../img/rus.png"); }
  .world-wide__item.kz {
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 40.19%, rgba(0, 0, 0, 0.8) 100%), no-repeat center/cover url("../img/kz.png"); }
  .world-wide__item.sa {
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 30.57%, rgba(0, 0, 0, 0.3) 100%), no-repeat center/cover url("../img/sa.jpg"); }

.world-wide__item-title {
  font-weight: 600;
  font-size: 24px;
  line-height: 29px;
  margin: 0 0 4px;
  color: #fff; }

.world-wide__item-desc {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #fff;
  margin: 0; }

@media (max-width: 1279px) {
  .world-wide__content {
    flex-direction: column; }
  .world-wide__main {
    width: 100%;
    margin: 0 0 20px; }
  .world-wide__slogan {
    min-height: 106px;
    max-width: 482px; }
  .world-wide__items {
    width: 100%;
    flex-direction: row; }
  .world-wide__item {
    width: 100%;
    height: 265px; }
    .world-wide__item.half {
      height: 265px; }
    .world-wide__item:not(:last-child) {
      margin-bottom: 20px; } }

@media (max-width: 767px) {
  .world-wide__main {
    padding: 16px;
    margin: 0 0 16px; }
  .world-wide__slogan {
    min-height: 130px;
    font-size: 20px;
    line-height: 24px; }
  .world-wide__item {
    padding: 24px 16px;
    height: 236px; }
    .world-wide__item:not(:last-child) {
